import { createStore } from "vuex";
import axios from "axios";
const store = createStore({
    state: {
        url: `https://api.dictionaryapi.dev/api/v2/entries/en/`,
        definition: null,
        error: null,
        word: '',
        meanings: null
    },
    mutations: {
        getDefinition(state, payload) {
            state.definition = payload
        },
        getError(state, payload) {
            if (payload.code == 'ERR_NETWORK') {
                state.error = payload
                state.definition = null
            } else if (payload.code == 'ERR_BAD_REQUEST') {
                state.definition = null
                state.error = payload.response.data
            }
        },
        getMeanings(state, payload) {
            state.meanings = payload
        },
        setNull(state) {
            state.definition = state.error = null
        }
    },
    actions: {
        async getDefiniton({ commit, state }, word) {
            try {
                state.word = word
                const res = await axios.get(`${state.url}${word}`)
                const data = res.data
                const { meanings, phonetics } = data[0]
                const definitionObj = { phonetics, word: word }
                commit('getMeanings', meanings)
                commit('getDefinition', definitionObj)
            } catch (error) {
                commit('getError', error)
            }
        }
    },
    getters: {}
})
export default store