<template>
  <div class="definition">
    <div class="container">
      <div class="definition__top">
        <h2 class="definition__word">{{ definition.word }}</h2>
        <div class="definition__wrapper">
          <div
            class="definition__phonetics"
            v-for="(item, index) in definition.phonetics"
            :key="index"
          >
            <span class="definition__phonetics-script">{{ item.text }}</span>
            <div class="definition__audio">
              <audio class="definition__audio-src"></audio>
              <button
                @click="play(item.audio, item)"
                :class="{ active: item.isPLayed }"
                class="definition__audio-play"
              >
                <img src="@/assets/images/play-icon.svg" alt="playIcon" />
              </button>
            </div>
          </div>
        </div>
      </div>
      <div class="definition__bottom">
        <div
          class="definition__bottom-item"
          v-for="(meaning, i) in meanings"
          :key="i"
        >
          <h2 class="definition__partOfSpeech">
            {{ meaning.partOfSpeech }}
          </h2>
          <ol class="definition__list">
            <li
              class="definition__meaning"
              v-for="(definition, i) in meaning.definitions"
              :key="i"
            >
              {{ definition.definition }}
            </li>
          </ol>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState(["definition", "meanings"]),
  },
  methods: {
    play(item, obj) {
      let audio = document.querySelector(".definition__audio-src");
      if (item == "") {
        audio.src = require("@/assets/audio/novoice.mp3");
      } else {
        audio.src = item;
      }
      obj.isPLayed = true;
      if (audio.paused) {
        audio.play();
      } else {
        audio.pause();
      }
      audio.addEventListener("ended", () => {
        obj.isPLayed = false;
      });
    },
  },
};
</script>