<template>
  <nav class="nav">
    <div class="container">
      <div class="nav__content">
        <a href="#" class="nav__logo">
          <img src="@/assets/images/logo2.svg" alt="logo" />
        </a>
        <div class="nav__field">
          <button class="nav__field-search" @click="search">
            <img src="@/assets/images/search.svg" alt="search" />
          </button>
          <input
            type="text"
            name="wordSearch"
            class="nav__field-inp"
            placeholder="Enter your word here..."
            v-model="word"
            @keydown.enter="search"
            @keypress.enter="search"
          />
          <button class="nav__field-cancel" @click="setNull(), (word = '')">
            <img src="@/assets/images/cancel.svg" alt="cancel" />
          </button>
        </div>
      </div>
    </div>
  </nav>
  <Modal v-if="open" @closeModal="open = $event" />
</template>

<script>
import { mapActions, mapMutations } from "vuex";
import Modal from "@/components/NavBarModal.vue";
export default {
  data() {
    return {
      word: "",
      open: false,
    };
  },
  components: {
    Modal,
  },
  methods: {
    ...mapActions(["getDefiniton"]),
    ...mapMutations(["setNull"]),
    search() {
      if (this.word) {
        this.getDefiniton(this.word);
        this.word = "";
      }
    },
  },
};
</script>